nav {
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

nav button {
  background: none;
  color: white;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
  font-size: 1.5em;
  border: none;
  margin: 1rem;
  cursor: pointer;
}

.progress {
  position: fixed;
  top: 0;
  left: 0;
  height: 5px;
  background: white;
  opacity: 0.5;
  transition: width 0.5s;
}

.container {
  height: 100vh;
  overflow: hidden;
  background: #017D5F;
  background: linear-gradient(to bottom left, #017D5F, #164C3F);
  color: white;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
  font-family: 'Cardo';
  font-size: 4rem;
  line-height: 1.5em;
}

.secret a {
  display: inline-block;
  margin: 2rem;
  font-size: 0.75em;
}